.preview-materials{padding:40px 0}.preview-materials__wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-align:center;-ms-flex-align:center;align-items:center}@media (max-width:599px){.preview-materials__wrapper{-webkit-box-orient:vertical;-webkit-box-direction:reverse;-ms-flex-direction:column-reverse;flex-direction:column-reverse}}.preview-materials__content{max-width:505px}.preview-materials__title{font-family:Roboto,sans-serif;font-weight:300;letter-spacing:1.6px;margin-bottom:20px;color:#002b2b;font-size:44px}@media (min-width:768px){.preview-materials__title{font-size:calc(44px + (2100vw - 16128px)/256)}}@media (min-width:1024px){.preview-materials__title{font-size:65px}}.preview-materials__title h2,.preview-materials__title h3{font-weight:300;font-size:44px}@media (min-width:768px){.preview-materials__title h2,.preview-materials__title h3{font-size:calc(44px + (2100vw - 16128px)/256)}}@media (min-width:1024px){.preview-materials__title h2,.preview-materials__title h3{font-size:65px}}.preview-materials__title b{font-weight:500}.preview-materials__text{font-family:Roboto,sans-serif;font-weight:300;letter-spacing:.14px;color:#002b2b;margin-bottom:30px;font-size:24px}@media (min-width:768px){.preview-materials__text{font-size:calc(24px + (400vw - 3072px)/256)}}@media (min-width:1024px){.preview-materials__text{font-size:28px}}.preview-materials__text b{font-weight:500}.preview-materials__image img{max-width:100%;max-height:100%}@media (max-width:767px){.preview-materials__image img{max-width:none;width:42vw}}@media (max-width:599px){.preview-materials__image{margin-bottom:30px}}