.content-section{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;background-position:50%;background-size:cover;min-height:400px}@media (min-width:768px){.content-section{min-height:calc(400px + (30000vw - 230400px)/256)}}@media (min-width:1024px){.content-section{min-height:700px}}.content-section{padding-top:50px}@media (min-width:768px){.content-section{padding-top:calc(50px + (5000vw - 38400px)/256)}}@media (min-width:1024px){.content-section{padding-top:100px}}.content-section{padding-bottom:50px}@media (min-width:768px){.content-section{padding-bottom:calc(50px + (5000vw - 38400px)/256)}}@media (min-width:1024px){.content-section{padding-bottom:100px}}.content-section__text{color:#fff;font-size:18px;text-shadow:0 0 20px rgba(0,0,0,.25);margin-bottom:20px}.content-section__text h3{text-shadow:0 0 20px rgba(0,0,0,.25);font-family:Roboto,sans-serif;font-size:40px;line-height:47px;font-weight:500;letter-spacing:.2px;margin-bottom:30px}.content-section__text p{font-size:18px;text-shadow:0 0 20px rgba(0,0,0,.25);margin-bottom:20px;color:#fff}.content-section__text a{font-size:18px;color:#00bfbf}