button{border:0}.button,button{outline:0;cursor:pointer}.button{display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;text-align:center;white-space:nowrap;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.button--border{min-height:60px;padding:15px 25px 14px;border-radius:10px;border:1px solid var(--green-color);color:var(--green-color);font-family:Roboto,sans-serif;font-size:18px;font-weight:500}@media (max-width:599px){.button--border{font-size:18px;line-height:24px;padding:11px 20px 13px;min-height:48px}}.button--border:hover{color:#fff;background:var(--green-color)}