.heading{color:#002b2b;margin-bottom:40px}@media (min-width:540px){.heading{margin-bottom:calc(40px + (6000vw - 32400px)/484)}}@media (min-width:1024px){.heading{margin-bottom:100px}}.heading--center{text-align:center}.heading--white{color:#fff}.heading__title{font-family:Roboto,sans-serif;font-weight:300;letter-spacing:.2px;text-align:center;font-size:28px}@media (min-width:768px){.heading__title{font-size:calc(28px + (1200vw - 9216px)/256)}}@media (min-width:1024px){.heading__title{font-size:40px}}.heading__description{font-size:22px;line-height:1.32;letter-spacing:-1px;color:#000;text-align:center;max-width:800px;margin:15px auto 0}@media (max-width:599px){.heading__description{margin:10px auto 0}}@media (max-width:854px){.heading__description br{display:none}}.heading__description a{position:relative;color:#000}.heading__description a:before{position:absolute;bottom:0;left:0;content:"";width:100%;height:1px;background:#000;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.heading__description a:hover:before{opacity:0;visibility:hidden}