:root{--white-color:#fff;--black-color:#1c1c1c;--green-color:#00bfbf;--gray-light-color:#f9f9f9;--gray-middle-color:#e1ddd7;--gold-color:#f0b46c;--gold-light-color:#b0a192;--blue-dark-color:#151723}::-moz-selection{background:#ccc;color:#4f4c66}::selection{background:#ccc;color:#4f4c66}*,:after,:before{-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-tap-highlight-color:transparent}*{margin:0;padding:0;outline:none}body,html{overflow-x:hidden}body{font-family:Roboto,sans-serif;font-size:14px;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-shadow:none;margin:0;color:var(--black-color)}@media (max-width:1023px){body{padding-top:70px}}button{cursor:pointer}b{font-family:Roboto,sans-serif;font-weight:500}a{text-decoration:none}ul{list-style:none}.link,h1,h2,h3,h4,h5,h6{font-weight:500}.link{font-size:18px;color:var(--green-color)}.h3,h3{font-family:Roboto,sans-serif}.height120{height:50px}@media (min-width:768px){.height120{height:calc(50px + (7000vw - 53760px)/256)}}@media (min-width:1024px){.height120{height:120px}}