.burger-menu{position:relative;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;outline:none;border:none;min-height:18px;margin-left:35px;background:none;display:none}@media (max-width:1023px){.burger-menu{display:-webkit-box;display:-ms-flexbox;display:flex}}@media (max-width:480px){.burger-menu{margin-left:15px}}.burger-menu__item{display:inline-block;width:28px;height:2px;background:#00bfbf;-webkit-transition:all .12s ease-out;-o-transition:all .12s ease-out;transition:all .12s ease-out;margin-bottom:6px}.burger-menu--active .burger-menu__item,.burger-menu__item:last-child{margin-bottom:0}.burger-menu--active .burger-menu__item:nth-child(2){opacity:0;visibility:hidden;-webkit-transform:translate(16px,7px);-ms-transform:translate(16px,7px);transform:translate(16px,7px)}.burger-menu--active .burger-menu__item:first-child{-webkit-transform:rotate(45deg) translate(5px,6px);-ms-transform:rotate(45deg) translate(5px,6px);transform:rotate(45deg) translate(5px,6px)}.burger-menu--active .burger-menu__item:last-child{-webkit-transform:rotate(-45deg) translate(-3px,2px);-ms-transform:rotate(-45deg) translate(-3px,2px);transform:rotate(-45deg) translate(-3px,2px)}